import React, { Component } from "react";
import {
  Grid,
  Header,
  Segment,
  Transition,
  Button,
  Icon
} from "semantic-ui-react";
import classes from "../../../static/css/Portal.css";
export default class TransitionablePortalExampleTransition extends Component {
  state = { animation: "scale", duration: 1200, open: false, qId: 0 };
  componentDidMount() {
    this.setState({ open: true });
  }

  Quotes = [
    {
      header: "Thomas Hauser",
      quote:
        'Not voting, is disrespecting the "best" of what our nation stands for.'
    },
    {
      header: "Unknown",
      quote: "The right of election is the very essence of the constitution."
    },
    {
      header: "Patty Berg",
      quote: "People are what any election is all about."
    }
  ];

  handleClickLeft = () => {
    let id = this.state.qId - 1;
    id = id % 3;
    this.setState({ qId: Math.abs(id) });
  };

  handleClickRight = () => {
    let id = this.state.qId + 1;
    id = id % 3;
    this.setState({ qId: id });
  };

  renderPortalContent = () => {
    return (
      <div className={classes.portal}>
        <h1>{this.Quotes[this.state.qId].header} : </h1>
        <p>{this.Quotes[this.state.qId].quote}</p>
      </div>
    );
  };

  render() {
    const { animation, duration, open } = this.state;

    return (
      <Grid.Row verticalAlign="bottom">
        <Grid.Column width={4} verticalAlign="middle">
          <Button onClick={this.handleClickLeft}>
            <Icon name="caret left" />
          </Button>
        </Grid.Column>
        <Grid.Column width={8} verticalAlign="middle">
          <Transition visible={open} transition={{ animation, duration }}>
            {this.renderPortalContent()}
          </Transition>
        </Grid.Column>
        <Grid.Column width={4} verticalAlign="middle" textAlign="center">
          <Button onClick={this.handleClickRight}>
            <Icon name="caret right" />
          </Button>
        </Grid.Column>
      </Grid.Row>
    );
  }
}
